import { useState } from 'react';
import './Calculator.css';
function Calculator()
{
    const[text,setText]=useState("0");
    function HandleInput(e)
    {
        setText(e.target.value);
    }
    function buttonClickDetect(e)
    {
        setText(text.concat(e.target.name));
    }
    function CancelAll()
    {
       setText('');
    } 
    const ClearOneByOne=()=>
    {
        setText(text.slice(0,text.length-1));
    }
    const   calculate=()=>
    {
        try{
            setText(eval(text).toString());
           }
        catch(error)
        {
            setText("Error");
        } 
    }
    return(
        <>
            <h1 className='h1Css'>  Simple Calculator App</h1>
            <input type="text" value={text} onChange={HandleInput}/>
        <div className="container">
       <div className='buttonHandleCss'>
        <button id='CancelAll' onClick={CancelAll}>Cancel</button>
        <button name='/' onClick={buttonClickDetect}>&divide;</button>
        <button name='*' onClick={buttonClickDetect}>*</button>
        <button id='clearOneByOne' onClick={ClearOneByOne}>Clear</button>
       </div>
       <div className='buttonHandleCss'>
        <button name='7' onClick={buttonClickDetect}>7</button>
        <button name='8' onClick={buttonClickDetect}>8</button>
        <button name='9' onClick={buttonClickDetect}>9</button>
        <button name='-' onClick={buttonClickDetect}>-</button>
       </div>
       <div className='buttonHandleCss'>
        <button name='4' onClick={buttonClickDetect}>4</button>
        <button name='5' onClick={buttonClickDetect}>5</button>
        <button name='6' onClick={buttonClickDetect}>6</button>
        <button name='+' onClick={buttonClickDetect}>+</button>
       </div>
       <div className='buttonHandleCss'>
        <button name='1' onClick={buttonClickDetect}>1</button>
        <button name='2' onClick={buttonClickDetect}>2</button>
        <button name='3' onClick={buttonClickDetect}>3</button>
        <button onClick={calculate}>=</button>
       </div>
       <div className='buttonHandleCss'>
        <button name='%' onClick={buttonClickDetect}>%</button>
        <button name='0' onClick={buttonClickDetect}>0</button>
        <button name='.' onClick={buttonClickDetect}>.</button>
       </div>
    
   </div>
        </>
    )
}
export default Calculator;